.select{
  margin: 1rem;
  position: relative;
  overflow: hidden;

}

select{
  color: #ff6f47;
  font-family:'Poppins' , sans-serif;
cursor: pointer;
width: 12rem;
}

/*arrow*/

.select::after{
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
background: #ff6f47;

cursor: pointer;
pointer-events: none;
}